 @import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-kr.css);
 @import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSans-jp.css);
:root{
  --primary: #efb6b2;
  --progress: #9be9a8;
  --secondary: #4e4e4e;
  --error: #ff4a4a;
  --delete: #efb6b2;
}

/* base styles & title */
body{
  font-family: "SpoqaHanSans Serif";
  color: var(--secondary);
}
.App{
  max-width: 960px;
  margin: 0 auto;
}
.title h1{
  color: var(--primary);
  font-size: 1.2rem;
  letter-spacing: 2px;
  font-weight: normal;
}
.title h2, .title p{
  text-align: center;
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}

/* upload form styles */
form{
  margin: 30px auto 10px;
  text-align: center;
}
label{
  display: block;
  width: 30px;
  height: 30px;
  border: 2px solid var(--primary);
  border-radius: 50%;
  margin: 10px auto;
  line-height: 25px;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label:hover{
  background: var(--primary);
  color: white;
}
.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: var(--error);
}

/* progress bar styles */
.progress-bar {
  height: 5px;
  background: var(--progress);
  margin-top: 20px;
}

/* image grid styles */

.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}


/* modal styles */

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}

.backdrop img {
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 70px auto 0;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  /* border: 1px solid #fff; */
}


 /* delete image */

 .delete {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: 2px solid var(--delete);
  border-radius: 50%;
  margin: 10px auto;
  color: var(--delete);
  font-weight: bold;
  font-size: 14px;
 }

 
 .delete:hover{
  background: var(--delete);
  color: white;
}